import React, {useEffect, useState} from "react"
import GoogleMapReact from 'google-map-react'
import axios from "axios"
import { useLocalStorage } from "../../../hooks/useLocalStorage"
import JobItem from "./JobItem"

const Dashboard = () => {
    // i had to used the whole url path because proxies dont seem to work 
    const REQUEST_ROUTE = 'https://coding-test.rootstack.net/api/jobs' 
    
    const [token] = useLocalStorage("token")
    const [jobs, setJobs] = useState([])
    
    // used to fetch jobs since first laod 
    useEffect(() => {
      async function getJobs () {
          const config = {
              headers: { Authorization: `Bearer ${token}` }
          }
          const response =  await axios.get(REQUEST_ROUTE, config)
          if(response?.data?.data) {
              setJobs(response?.data?.data)
          }
      }
      getJobs()
    }, [])

    return (
      
      <div style={{ height: '100vh', width: '100%' }}>
        <GoogleMapReact
          center={[59.938043, 30.337157]}
          zoom={1}
          hoverDistance={20}
          >
          {jobs.length > 0 && jobs.map(({ id, latitude, longitude, title }) => ( // destructured the data
              <JobItem 
              key={id} 
              lat={latitude} 
              lng={longitude} 
              text={title} />
          ))}
        </GoogleMapReact>

      </div>
    )
}

export default Dashboard