import   React, { useState, useEffect } from "react"
import  axios from 'axios'
import { useLocalStorage } from "../../../hooks/useLocalStorage"
import ErrorMessage from "./ErrorMessage"

import { useNavigate } from "react-router-dom";
 
const Login = () => {
  const REQUEST_ROUTE = 'https://coding-test.rootstack.net/api/auth/login'
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [accessToken, setAccessToken] = useState('')
  const [_, setToken] = useLocalStorage('token')

  const navigate = useNavigate()

  // verify first if the session exists 
  useEffect(() => {
     if (accessToken) {
        setToken(accessToken)
     }
  }, [accessToken]) //

  const handleLogin = async (event) => {
    event.preventDefault()

    setLoading(true)
    try {
      const response =  await axios.post(REQUEST_ROUTE, {
        email,
        password
      })

      setLoading(false)

      const validToken = response?.data?.access_token // ES7 ? operand in case the server modifies the json
      if (validToken) {
          setAccessToken(validToken)

          navigate('/dashboard')
      }
    } catch (error) {
        if (error && error.message) { 
            setError(error.message)
        }
        console.log(error) // could be the case that is not a network error 
    }
    setLoading(false)
    setEmail('')
    setPassword('')
  }

  return (
    <div className="col-6">
        <div className={`alert alert-danger mt-1 ${error? 'visible': 'invisible'}`} role="alert"><ErrorMessage message={error} /></div>
        <h3 className="text-center">Sign In </h3>
        <form onSubmit={e =>  handleLogin(e)}>
          <div className="form-group">
            <label htmlFor="email">Email address</label>
            <input
            onChange={(e) => {setEmail(e.target.value)}}
            value={email}
            id="email"
            name="email"
            type="text"
            className="form-control"
            />
          </div>
          <div className="form-group">
            <label htmlFor="password">Password</label>
            <input
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            id="password"
            name="password"
            type="password"
            className="form-control"
            />
          </div>
          <div className="form-group mt-4">
            <button
            disabled={loading}
            name="submit"
            type="submit"
            className="btn btn-primary form-control"
            >
            Login
            </button>
          </div>
        </form>
    </div>
  )
}

export default Login


